.button {

    &__search-button {
        background-color: #81b3d2;
        width: 280px;
        height: 58px;
        color: #ffffff;
        text-transform: uppercase;
        font-size: 18px;
        line-height: 26px;
        font-weight: bold;
        position: relative;
        left: 20px;
        top: 36px;
        border: none;
        // display: inline-block;
    }

    &__search-button:hover {
        background-color: #669ec0;
        border: none;
    }

    &__search-button:active {
        background-color: #5496bd;
        border: none;
        color: #94b9d2;
    }

    &__search-button:visited {
        background-color: #e5e5e5;
        border: none;
        color: #ffffff;
    }
}