@media (min-width: @mobile-width) {
.main-footer {
    position: relative;
    top: 62px;
    width: 320px;
    box-sizing: border-box;
    
    &__hashtag a {
        font-size: 21px;
        line-height: 26px;
        font-weight: bold;
        text-align: center;
        color: #000000;
    }

    &__hashtag {
        text-align: center;
        margin-top: 30px;
    }

    &__mobile-photo {
        position: relative;
        top: -47px;
    }
}
}

